.example-list {
    max-width: 100%;
    min-height: 50px;
    display: block;
    overflow: hidden;
}

.example-box {
    word-wrap: break-word;
    /* 단어 단위로 줄바꿈 허용 */
    overflow-wrap: break-word;
    /* word-wrap과 동일한 기능, IE 호환성 */
    white-space: normal;
    /* 기본 줄바꿈 동작 */
    min-height: 50px;
    padding: 10px;
    border-bottom: none;
    /* 기존 border-bottom 제거 */
    color: rgba(0, 0, 0, 1);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    font-size: 14px;
}

.user-message {
    padding: 10px;
    display: flex;
    justify-content: flex-end; /* 자식 요소를 오른쪽 정렬 */
}

/* 봇 메시지 스타일 */
.bot-message {
    /* 흰색 배경 */
    /* 둥근 모서리 */
    width: 100%;
}

.cdk-drag-preview {
    box-sizing: border-box;
    width: 100px;
    height: 100px;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
        0 8px 10px 1px rgba(0, 0, 0, 0.14),
        0 3px 14px 2px rgba(0, 0, 0, 0.12);

}

.cdk-drag-animating {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.example-box:last-child {
    border: none;
}

.example-list.cdk-drop-list-dragging .example-box:not(.cdk-drag-placeholder) {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
}

.example-custom-placeholder {
    background: rgba(204, 204, 204, 0.1);
    /* 반투명 배경, 기존 #ccc 색상 유지 */
    backdrop-filter: blur(10px);
    /* 흐림 효과 */
    border: 1px solid rgba(255, 255, 255, 0.2);
    /* 밝은 테두리, 점선 제거 */
    min-height: 100px;
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
    /* 기존 transition 유지 */
    /* 그림자 효과 */
}

.example-box .item-content {
    display: flex;             /* Flexbox 사용 */
    flex-direction: column;    /* 세로 배치 */
    align-items: flex-start;  
}

.example-box .item-title {
    font-weight: bold;        /* 제목 굵게 */
}

.example-box .item-desc {
    font-size: smaller;       /* 설명 작은 글씨 */
    margin-top: 5px;          /* 제목과 설명 사이 간격 */
}

/* component.css */
.gemini-data {
}

.item-desc pre {
    white-space: pre-wrap;       /* 줄 바꿈 유지하면서 넓이에 맞춰 자동 줄 바꿈 */
    word-wrap: break-word;       /* 긴 단어를 줄 바꿈하여 넘어가지 않도록 설정 */
    overflow-x: auto;            /* 가로 스크롤바 생성 */
    max-width: 100%;             /* 최대 넓이 제한 */
  }